@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,
h2,
h3,
p {
  margin: 0;
}
input,
textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading,
.error {
  text-align: center;
  margin: 40px auto;
}

.App {
  min-height: 100%;
}

.night-mode {
  background-color: #333;
}

.navbar {
  background: #58249c;
  padding: 20px;
  color: #fff;
}
.navbar nav {
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.navbar a.brand {
  margin-right: auto;
  color: #fff;
  text-decoration: none;
}

.navbar a:last-child {
  color: #fff;
  text-decoration: none;
  margin-left: 40px;
  padding: 4px 8px;
  border: 1.5px solid #fff;
  border-radius: 8px;
}

.navbar a:last-child:hover {
  background: #fff;
  color: #333;
}

.searchbar form {
  display: flex;
  align-items: center;
}

.searchbar input {
  margin-left: 10px;
}

.home {
  max-width: 1200px;
  margin: 60px auto;
}

.recipe-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  gap: 40px;
  max-width: 1200px;
  margin: 40px auto;
}

.recipe-list .card {
  background: #fff;
  color: #333;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  position: relative;
}

.recipe-list .card:hover {
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}

.recipe-list .card h3 {
  color: #555;
  margin-bottom: 6px;
}

.recipe-list .card p {
  color: #999;
  font-size: 0.9em;
}
.recipe-list .card div {
  color: #555;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}
.recipe-list .card a {
  color: #555;
  text-decoration: none;
  display: block;
  background: #e2e2e2;
  font-size: 0.9em;
  width: 120px;
  text-align: center;
  padding: 4px 8px;
  border-radius: 4px;
  margin: 20px auto 0;
}

.recipe-list .card.dark-mode {
  background-color: #555;
}

.recipe-list .card.dark-mode h3,
.recipe-list .card.dark-mode h4,
.recipe-list .card.dark-mode div,
.recipe-list .card.dark-mode .page-title {
  color: #fff;
}
.recipe-list .card.dark-mode li,
.recipe-list .card.dark-mode p {
  color: rgb(235, 235, 235);
}

.recipe-list .delete {
  position: absolute;
  top: 10px;
  right: 10px;
  -webkit-filter: invert(60%);
          filter: invert(60%);
  font-size: 1.5rem;
  cursor: pointer;
}
.recipe-list .delete:hover {
  color: #f34343;
  -webkit-filter: none;
          filter: none;
}

.recipe-list.dark-mode .delete {
  color: #fff;
}

.create {
  color: #555;
  max-width: 450px;
  margin: 40px auto;
}

.create label span {
  display: block;
  margin: 30px 0 10px;
}

.create p {
  margin-top: 10px;
  font-size: 0.8em;
}

.create button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
}

.create .ingredients {
  display: flex;
  align-items: center;
}
.create .ingredients .btn {
  margin-left: 10px;
}


.recipe {
  max-width: 1200px;
  margin: 40px auto;
  text-align: center;
  background: #fff;
  padding: 40px;
  position: relative;
}

.recipe ul {
  display: flex;
  padding: 0;
  justify-content: center;
}
.recipe li {
  list-style: none;
  margin-right: 10px;
  color: #777;
}

.recipe li::after {
  content: ",";
}

.method {
  text-align: left;
  line-height: 1.5em;
}
.recipe h4 {
  margin: 15px 0 5px;
}
.recipe .go-back {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: #eee;
  color: #222;
  padding: 3px 5px;
  border-radius: 3px;
  border: none;
  cursor: pointer;
}

.recipe .go-back:hover {
  background: #444;
  color: #fff;
}
.recipe .delete {
  position: absolute;
  top: 1rem;
  right: 3rem;
  color: #222;
  padding: 5px;
  font-size: 1.5rem;
  border-radius: 3px;
  cursor: pointer;
}

.recipe.dark-mode .delete {
  color: rgb(204, 204, 204);
  -webkit-filter: none;
          filter: none;
}
.recipe .delete:hover {
  color: #f34343;
  -webkit-filter: none;
          filter: none;
}

.recipe.dark-mode {
  background: #555;
}

.recipe.dark-mode h1,
.recipe.dark-mode h2,
.recipe.dark-mode h4,
.recipe.dark-mode .page-title {
  color: #fff;
}
.recipe.dark-mode li,
.recipe.dark-mode p {
  color: rgb(235, 235, 235);
}

.theme-selector {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 20px auto auto;
}
.theme-selector .switch .fa-sun {
  color: rgb(255, 255, 255);
}
.theme-selector .switch {
  transition: all 0.5s;
  cursor: pointer;
}

.theme-selector span {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 10px;
  cursor: pointer;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
}
.theme-selector .purple {
  background-color: #58249c;
}
.theme-selector .green {
  background-color: seagreen;
}
.theme-selector .red {
  background-color: #b41111;
}

